import React from 'react';
import $http from '../request';
import logger from '../logger';

class ButtonGroup extends React.Component {
    constructor(props) {
        super(props);
        if(!this.props.id) {
            throw new Error('property ID is required on Toggle');
        }
        this.sendOn = this.sendOn.bind(this);
        this.sendOff = this.sendOff.bind(this);
    }

    sendOn() {
        console.log(this.props.id);
        // http://192.168.0.19:8080/json.htm?type=devices&used=true&filter=all&favorite=1
        // if response has var data, then data.result is an array. e.g. data.result[0].name === "Living room"
        $http('/' + this.props.id + '/on')
            .then(data => {
                if(data.status !== 'received') {
                    logger.error('error on sendOn: ' + data.status);
                }
            })
            .catch(error => logger.error('error on sendOn: ' + error));
    }

    sendOff() {
        $http('/' + this.props.id + '/off')
            .then(data => {
                if(data.status !== 'received') {
                    logger.error('error on sendOff: ' + data.status);
                }
            })
            .catch(error => logger.error('error on sendOff: ' + error));
    }

    render() {
        let icon = <i></i>;
        if(this.props.icon) {
            icon = <i className={'glyphicon glyphicon-' + this.props.icon}></i>;
        }
        return (
            <div className="btn-group btn-group-justified margin-top">
                <a href="#" className="btn btn-default" onClick={this.sendOn}>
                    <i className="glyphicon glyphicon-plus-sign"></i>
                </a>{/* for this.onChange, the bind is done in the constructor */}
                <span className="btn" disabled>
                    {icon}
                    {this.props.label}
                </span>
                <a href="#" className="btn btn-default" onClick={this.sendOff}>
                    <i className="glyphicon glyphicon-minus-sign"></i>
                </a>
            </div>
        );
    }
}
export default ButtonGroup;


